import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { FrameListService } from 'src/app/services/frame-list.service';
import { StateAppService } from 'src/app/services/state-app.service';

@Component({
  selector: 'app-full-viewer',
  templateUrl: './full-viewer.component.html',
  styleUrls: ['./full-viewer.component.scss']
})
export class FullViewerComponent implements OnInit {
  index = 0;
  intervalAnimation: any;
  selectedSizeCanvas: string;
  frameUrl: any;

  constructor(
    private framesList: FrameListService,
    public stateApp: StateAppService
  ) {}

  ngOnInit() {
    this.startAnimation();
    this.selectedSizeCanvas = this.stateApp.state.canvasSize.toString();
  }

  startAnimation() {
    clearInterval(this.intervalAnimation);
    this.index = 0;
    this.intervalAnimation = setInterval(
      this.animationFrames.bind(this),
      1000 / this.stateApp.state.fps
    );
  }

  animationFrames() {
    if (this.index > this.framesList.getFramesUrl().length - 2) {
      this.index = 0;
    } else {
      this.index += 1;
    }
    this.frameUrl = this.framesList.getFramesUrl()[this.index];
    // requestAnimationFrame(this.animationFrames.bind(this));
  }

}
