import {
  Component,
  OnInit,
  OnDestroy,
  ViewChild,
  ElementRef
} from '@angular/core';
import { StateAppService } from '../services/state-app.service';
import { FrameListService } from '../services/frame-list.service';
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {
  @ViewChild('canvas', { static: true })
  canvas: ElementRef<HTMLCanvasElement>;
  private ctx: CanvasRenderingContext2D;
  private widthCanvas: number;
  private heightCanvas: number;
  private isDrawing = false;
  private isEraser = false;
  nameFile: string;
  title = 'simple-piskel-clone';
  lastX: number;
  lastY: number;
  public frameUrl: any;

  constructor(
    public stateApp: StateAppService,
    private framesList: FrameListService
  ) {}

  ngOnInit(): void {
    this.ctx = this.canvas.nativeElement.getContext('2d');
    this.ctx.save();
    this.heightCanvas = this.canvas.nativeElement.height;
    this.widthCanvas = this.canvas.nativeElement.width;
    this.nameFile = this.stateApp.state.nameFile;

    const image = new Image();
    image.src = this.framesList.getFramesUrl()[this.stateApp.state.currentFrame];
    this.ctx.drawImage(image, 0, 0);
    this.onSaveCtx();
  }
  // draw
  canvasMouseMove(ev: MouseEvent) {
    switch (this.stateApp.state.currentTool) {
      case 'Pen':
        this.drawPen(ev.offsetX, ev.offsetY);
        break;
      case 'Eraser':
        this.eraser(ev.offsetX, ev.offsetY);
        break;

      default:
        break;
    }
  }

  drawPen(x, y) {
    if (!this.isDrawing) {
      return;
    }
    this.ctx.beginPath();
    this.ctx.fillStyle = this.stateApp.state.currentColor;
    const heightPix =
      (this.heightCanvas / this.stateApp.state.canvasSize) *
      this.stateApp.state.penSize;
    const widthPix =
      (this.widthCanvas / this.stateApp.state.canvasSize) *
      this.stateApp.state.penSize;
    const dotX = Math.floor(x / widthPix) * widthPix;
    const dotY = Math.floor(y / heightPix) * heightPix;
    this.ctx.fillRect(dotX, dotY, widthPix, heightPix);
    this.calcStraightLine(this.lastX, this.lastY, dotX, dotY).forEach(dot => {
      this.ctx.fillRect(dot[0], dot[1], widthPix, heightPix);
    });
    [this.lastX, this.lastY] = [dotX, dotY];
  }

  noDrawing(event) {
    switch (this.stateApp.state.currentTool) {
      case 'Stroke':
        const x = Math.floor(event.offsetX);
        const y = Math.floor(event.offsetY);
        this.calcStraightLine(this.lastX, this.lastY, x, y).forEach(element => {
          this.drawPen(element[0], element[1]);
        });
        break;

      default:
        this.isDrawing = false;
        this.isEraser = false;
        this.onSaveCtx();
        break;
    }
    this.isDrawing = false;
    this.isEraser = false;
    this.onSaveCtx();
    return this.isDrawing;
  }

  yesDrawing(ev: MouseEvent) {
    switch (this.stateApp.state.currentTool) {
      case 'Pen':
        this.isDrawing = true;
        [this.lastX, this.lastY] = [ev.offsetX, ev.offsetY];
        this.drawPen(ev.offsetX, ev.offsetY);
        break;
      case 'Eraser':
        this.isEraser = true;
        this.eraser(ev.offsetX, ev.offsetY);
        break;
      case 'Bucket':
        this.fillBucket(ev.offsetX, ev.offsetY);
        break;
      case 'Stroke':
        this.isDrawing = true;
        [this.lastX, this.lastY] = [
          Math.floor(ev.offsetX),
          Math.floor(ev.offsetY)
        ];
        break;
      default:
        break;
    }
  }

  eraser(x, y) {
    if (!this.isEraser) {
      return;
    }
    this.ctx.beginPath();
    this.ctx.fillStyle = this.stateApp.state.currentColor;
    const heightPix =
      (this.heightCanvas / this.stateApp.state.canvasSize) *
      this.stateApp.state.penSize;
    const widthPix =
      (this.widthCanvas / this.stateApp.state.canvasSize) *
      this.stateApp.state.penSize;
    const dotX = Math.floor(x / widthPix) * widthPix;
    const dotY = Math.floor(y / heightPix) * heightPix;
    this.ctx.clearRect(dotX, dotY, widthPix, heightPix);
    this.onSaveCtx();
  }

  onSaveCtx() {
    this.frameUrl = this.canvas.nativeElement.toDataURL();
    this.framesList.upDateFrameUrl(this.stateApp.state.currentFrame, this.frameUrl);
    this.framesList.save();
  }



  resetCtx() {
    this.ctx.restore();
    this.ctx.clearRect(0, 0, this.widthCanvas, this.heightCanvas);
  }

  editFrame(index: number) {
    this.resetCtx();
    const image = new Image();
    image.src = this.framesList.getFramesUrl()[index];
    this.ctx.drawImage(image, 0, 0);
    this.onSaveCtx();
  }

  // bucket
  pixelColorGet(x, y) {
    return this.ctx.getImageData(x, y, 1, 1).data;
  }

  fillBucket(dX, dY) {
    const heightPix =
      (this.heightCanvas / this.stateApp.state.canvasSize) *
      this.stateApp.state.penSize;
    const widthPix =
      (this.widthCanvas / this.stateApp.state.canvasSize) *
      this.stateApp.state.penSize;
    const pixX = Math.floor(dX / widthPix) * widthPix;
    const pixY = Math.floor(dY / heightPix) * heightPix;
    const pixXYs = [];
    pixXYs.push({ dx: pixX, dy: pixY });
    const colorOld = Array.from(this.pixelColorGet(pixX, pixY));
    const around = [
      { deltax: -1, deltay: 0 },
      { deltax: 1, deltay: 0 },
      { deltax: 0, deltay: -1 },
      { deltax: 0, deltay: 1 }
    ];
    this.ctx.fillStyle = this.stateApp.state.currentColor;
    for (const dot of pixXYs) {
      around.forEach(delta => {
        const x = dot.dx + delta.deltax * widthPix;
        const y = dot.dy + delta.deltay * heightPix;
        const currentColorPixel = Array.from(this.pixelColorGet(x, y));
        if (
          colorOld.toString() === currentColorPixel.toString() &&
          !pixXYs.some(p => p.dx === x && p.dy === y) &&
          x >= 0 &&
          y >= 0 &&
          x < this.widthCanvas &&
          y < this.heightCanvas
        ) {
          pixXYs.push({ dx: x, dy: y });
        }
      });
    }

    for (const dot of pixXYs) {
      setTimeout(() => {
        this.ctx.fillRect(dot.dx, dot.dy, widthPix, heightPix);
      }, 1);
    }
    this.onSaveCtx();
  }

  calcStraightLine(lastDX, lastDY, currentDX, currentDY) {
    if (lastDX === currentDX && lastDY === currentDY) {
      return [];
    }
    const coordinatesArray = [];
    let lastdx = lastDX;
    let lastdy = lastDY;
    const dx = Math.abs(currentDX - lastdx);
    const dy = Math.abs(currentDY - lastdy);
    const sx = lastdx < currentDX ? 1 : -1;
    const sy = lastdy < currentDY ? 1 : -1;
    let err = dx - dy;
    coordinatesArray.push([lastdx, lastdy]);
    while (!(lastdx === currentDX && lastdy === currentDY)) {
      const e2 = err * 2 ** 1;
      if (e2 > -dy) {
        err -= dy;
        lastdx += sx;
      }
      if (e2 < dx) {
        err += dx;
        lastdy += sy;
      }
      coordinatesArray.push([lastdx, lastdy]);
    }
    return coordinatesArray;
  }
}
