import { Component, OnInit, Input, Output, EventEmitter, DoCheck, HostListener } from '@angular/core';
import { FrameListService } from '../services/frame-list.service';
import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { StateAppService } from '../services/state-app.service';

@Component({
  selector: 'app-list-frame',
  templateUrl: './list-frame.component.html',
  styleUrls: ['./list-frame.component.scss']
})
export class ListFrameComponent implements OnInit, DoCheck {
  @Input() frameUrl: any;
  @Output() resetCtx = new EventEmitter<boolean>();
  @Output() eventEditFrame = new EventEmitter<number>();


  constructor(public framesList: FrameListService, private stateApp: StateAppService) {
  }

  ngOnInit() {

  }

  ngDoCheck() {
    if (this.frameUrl !== undefined) {
    this.framesList.upDateFrameUrl(this.stateApp.state.currentFrame, this.frameUrl);
 }
  }

  addFrame() {
    this.framesList.addFrameUrl(this.frameUrl);
    this.resetCtx.emit();
  }

  drop(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.framesList.getFramesUrl(), event.currentIndex, event.previousIndex);
  }

  editFrame(index: number) {
    this.eventEditFrame.emit(index);
  }
}
