import { Injectable, OnDestroy, OnChanges } from '@angular/core';
import { StateApp } from '../models/appState';

@Injectable({
  providedIn: 'root'
})
export class StateAppService {
  state: StateApp;

  constructor() {
    this.state = JSON.parse(localStorage.getItem('STATE'));
    if (this.state === null) {
      this.state = {
        currentColor: 'red',
        previousColor: 'blue',
        currentTool: 'pen',
        penSize: 4,
        canvasSize: 32,
        fps: 1,
        currentFrame: 0,
        nameFile: 'New Piskel'
      };
    }
    this.state.currentFrame = 0;
  }

  save(state: StateApp) {
    localStorage.setItem('STATE', JSON.stringify(state));
  }

  get(): StateApp {
    let state = JSON.parse(localStorage.getItem('STATE'));
    if (state === null) {
      state = {
        currentColor: 'red',
        previousColor: 'blue',
        currentTool: 'pen',
        penSize: 4,
        canvasSize: 32,
        fps: 1,
        currentFrame: 0,
        nameFile: 'New Piskel'
      };
    }
    return state;
  }
}
