import { Component, OnInit, ViewChild, ElementRef, Input, Output, EventEmitter } from '@angular/core';
import { FrameListService } from 'src/app/services/frame-list.service';
import { StateAppService } from 'src/app/services/state-app.service';

@Component({
  selector: 'app-frame',
  templateUrl: './frame.component.html',
  styleUrls: ['./frame.component.scss']
})
export class FrameComponent implements OnInit {
  @Input() frameUrl: any;
  @Input() index: number;
  @Output() eventEditFrame = new EventEmitter<number>();

  private ctx: CanvasRenderingContext2D;

  constructor(private framesList: FrameListService, private stateApp: StateAppService ) { }

  ngOnInit() {
  }

  delete(index: number) {
    this.stateApp.state.currentFrame = index;
    this.framesList.deleteFrameUrl(index);
  }

  dublicate(index: number) {
    this.framesList.copyFrameUrl(index);
  }

  editFrame(index) {
    this.stateApp.state.currentFrame = index;
    this.eventEditFrame.emit(index);
  }
}
