import { Injectable } from '@angular/core';
import { StateAppService } from './state-app.service';
import * as GIF from '../../assets/js/gif.js';

@Injectable({
  providedIn: 'root'
})
export class FrameListService {
 framesUrls = [];
 init = true;
 gif: GIF;
 blob;

  constructor(private stateApp: StateAppService) {

      const arrayUrl = JSON.parse(localStorage.getItem('FRAMES'));
      if (arrayUrl === null ) {
        this.framesUrls.push('data:text/plain;base64,');
        this.stateApp.state.currentFrame = 0;
      } else {
        this.framesUrls = arrayUrl;
        this.stateApp.state.currentFrame = this.framesUrls.length - 1;
      }
  }

  save() {
    localStorage.setItem('FRAMES', JSON.stringify(this.framesUrls));
  }

  addFrameUrl(frameUrl) {
    this.framesUrls.push(frameUrl);
    this.stateApp.state.currentFrame = this.framesUrls.length - 1;
    this.save();
  }
  getFramesUrl() {
    return this.framesUrls;
  }

  upDateFrameUrl(index: number, frameUrl: any) {
    this.framesUrls[index] = frameUrl;
    this.save();
  }

  deleteFrameUrl(index: number) {
  console.log('delete', index);
  console.log(this.framesUrls.length);
  this.framesUrls.splice(index, 1);
  console.log(this.framesUrls.length);
  console.log(this.framesUrls);
  this.save();
  }

  copyFrameUrl(index: number) {
    const arr1 = this.framesUrls.slice(0, index);
    const arr2 = this.framesUrls.slice(index);
    this.framesUrls = [...arr1, this.framesUrls[index], ...arr2];
    this.stateApp.state.currentFrame += 1;
    this.save();
  }

  async createGif() {
    this.gif = new GIF({
      workers: 2,
      workerScript: '../../assets/js/gif.worker.js',
      quality: 10
    });
    this.getFramesUrl().forEach((frame) => {
      const image = new Image();
      image.src = frame;
      this.gif.addFrame(image, {copy: true});
    });
    
    this.blob = await this.gif.on('finished', (blob) => {
      return blob;
    });
    this.gif.render();
    return { gif: this.gif, blob: this.blob };
  }


}
