import { Component, OnInit } from '@angular/core';
import { StateApp } from '../models/appState';
import { StateAppService } from '../services/state-app.service';

@Component({
  selector: 'app-tools-panel',
  templateUrl: './tools-panel.component.html',
  styleUrls: ['./tools-panel.component.scss']
})
export class ToolsPanelComponent implements OnInit {

  state: StateApp;
  selectedPen = false;
  selectedEraser = false;
  selectedBucket = false;
  selectedStroke = false;
  constructor(private stateApp: StateAppService) {
    this.state = this.stateApp.state;
  }

  ngOnInit() {
    let div = document.querySelector('.prev') as HTMLDivElement;
    div.style.backgroundColor =  this.state.previousColor;
    div = document.querySelector('.current') as HTMLDivElement;
    div.style.backgroundColor =  this.state.currentColor;
    this.selectedTool();
  }

  chooseTools(ev: Event) {
    const btnTools = (ev.target as HTMLTextAreaElement).closest('div');
    console.log(btnTools.className);
    switch (btnTools.className) {
      case 'tool pen':
        this.state.currentTool = 'Pen';
        break;
      case 'tool bucket':
        this.state.currentTool = 'Bucket';
        break;
      case 'tool eraser':
        this.state.currentTool = 'Eraser';
        break;
      case 'tool stroke':
        this.state.currentTool = 'Stroke';
        break;

      default:
        break;
    }
    this.stateApp.save(this.state);
    this.selectedTool();
  }

  chooseSizePen(ev: Event) {
    const btnSizePen = (ev.target as HTMLTextAreaElement).closest('div');
    console.log(btnSizePen.className);
    switch (btnSizePen.className) {
      case 'btn pixel1':
        this.state.penSize = 1;
        break;
      case 'btn pixel2':
        this.state.penSize = 2;
        break;
      case 'btn pixel3':
        this.state.penSize = 3;
        break;
      case 'btn pixel4':
        this.state.penSize = 4;
        break;
      default:
        break;
    }
    this.stateApp.save(this.state);
  }

  chooseColors(ev: Event) {
    const btnChooseColors = (ev.target as HTMLTextAreaElement).closest('div');
    console.log(btnChooseColors.className);
    switch (btnChooseColors.className) {
      case 'current':
        this.state.penSize = 1;
        break;
      case 'prev':
        this.state.penSize = 2;
        break;
    }
    this.stateApp.save(this.state);
  }

  changeColorCurrent(ev: Event) {
    console.log((ev.target as HTMLTextAreaElement).value);
    const div = document.querySelector('.current') as HTMLDivElement;
    div.style.backgroundColor =  (ev.target as HTMLTextAreaElement).value;
    this.state.currentColor = (ev.target as HTMLTextAreaElement).value;
    this.stateApp.save(this.state);
  }

  changeColorPrev(ev: Event) {
    console.log((ev.target as HTMLTextAreaElement).value);
    const div = document.querySelector('.prev') as HTMLDivElement;
    div.style.backgroundColor =  (ev.target as HTMLTextAreaElement).value;
    this.state.previousColor = (ev.target as HTMLTextAreaElement).value;
    this.stateApp.save(this.state);
  }

  changeCurrentPrev() {
    const temp = this.state.previousColor;
    let div = document.querySelector('.prev') as HTMLDivElement;
    div.style.backgroundColor =  this.state.currentColor;
    this.state.previousColor = this.state.currentColor;

    div = document.querySelector('.current') as HTMLDivElement;
    div.style.backgroundColor =  temp;
    this.state.currentColor = temp;
    this.stateApp.save(this.state);
  }

  selectedTool() {
    this.selectedBucket = false;
    this.selectedPen = false;
    this.selectedEraser = false;
    this.selectedStroke = false;
    switch (this.stateApp.state.currentTool) {
      case 'Pen':
        this.selectedPen = true;
        break;
      case 'Eraser':
        this.selectedEraser = true;
        break;
        case 'Bucket':
          this.selectedBucket = true;
          break;
        case 'Stroke':
            this.selectedStroke = true;
            break;
      default:
        break;
    }
  }
}
