import { Component, OnInit, ViewChild, ElementRef, Input } from '@angular/core';
import { FrameListService } from '../services/frame-list.service';
import { StateAppService } from '../services/state-app.service';
import { MatRadioChange } from '@angular/material/radio';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-pre-viewer',
  templateUrl: './pre-viewer.component.html',
  styleUrls: ['./pre-viewer.component.scss']
})
export class PreViewerComponent implements OnInit {
  index = 0;
  intervalAnimation: any;
  selectedSizeCanvas: string;
  frameUrl: any;
  hrefSaveGif;

  constructor(
    private framesList: FrameListService,
    public stateApp: StateAppService,
    private sanitizer: DomSanitizer
  ) {}

  ngOnInit() {
    this.startAnimation();
    this.selectedSizeCanvas = this.stateApp.state.canvasSize.toString();
  }

  startAnimation() {
    clearInterval(this.intervalAnimation);
    this.index = 0;
    this.intervalAnimation = setInterval(
      this.animationFrames.bind(this),
      1000 / this.stateApp.state.fps
    );
  }

  animationFrames() {
    if (this.index > this.framesList.getFramesUrl().length - 2) {
      this.index = 0;
    } else {
      this.index += 1;
    }
    this.frameUrl = this.framesList.getFramesUrl()[this.index];
    // requestAnimationFrame(this.animationFrames.bind(this));
  }
  setupFps(event) {
    this.stateApp.state.fps = event.value;
    this.startAnimation();
  }

  selectSizeCanvas(event: MatRadioChange) {
    this.stateApp.state.canvasSize = event.value;
  }

  async saveGif() {
    const {gif, blob} = await this.framesList.createGif();
    gif.on('finished', (blob) => {
      this.hrefSaveGif = URL.createObjectURL(blob);
      window.open(URL.createObjectURL(blob));
      this.hrefSaveGif = window.URL.createObjectURL(blob);
      this.hrefSaveGif = this.sanitizer.bypassSecurityTrustUrl(this.hrefSaveGif);
    });
  }
}
